






















import Vue from 'vue';
import { convertEmoji } from '@/utils/unicode';
import { Country } from '../../api/country';

export default Vue.extend({
  name: 'Card',
  props: {
    input: Object as () => Country,
  },
  computed: {
    country(): Country {
      return this.input;
    },
    flag(): string {
      return convertEmoji(this.country.code);
    },
  },
});
